.footer {
    height: 300px;
    background-color: #f3ffe5;
    display: flex;
    justify-content: space-around;

}
.container {
    width: 80%;
    border: 1px solid;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    padding: 80px 0;
}
.socialBlock {

    display: flex;
    justify-content: space-between;
    width: 500px;
    border: 1px solid;

}
.socialIcon {
    height: 60px;
    width: 60px;
    background-color: #ededed;
}

